<template>
  <div :class="prefixClassname('captcha-link')">
    <van-count-down v-if="sent" :time="time" format="sss后重新获取" @finish="emit('finish')" />
    <div v-else @click="emit('send')">
      <slot />
    </div>
  </div>
</template>

<script>
import { defineComponent } from "vue"
import { prefixClassname } from "@/styles/prefix"
import { CountDown } from "vant"

export default defineComponent({
  name: "CaptchaLink",
  components: {
    [CountDown.name]: CountDown,
  },
  props: {
    sent: Boolean,
    time: [String, Number],
  },
  setup(props, { emit }) {
    return {
      prefixClassname,
      emit,
    }
  },
})
</script>

<style lang="scss">
@import "../styles/prefix";
@import "../styles/variables";

.#{$component-prefix}captcha-link {
  color: $text-link-color;

  .van-count-down {
    color: $gray-6;
  }
}
</style>
