<template>
  <div :class="prefixClassname('forgot')">
    <div :class="prefixClassname('forgot__header')">
      <div :class="prefixClassname('forgot__title')">修改登录密码</div>
      <div :class="prefixClassname('forgot__tips')">
        <van-icon name="info-o" />
        <span>密码由8~16位数字、字母任意组合</span>
      </div>
    </div>
    <Form :class="prefixClassname('forgot__form')">
      <van-field
        :class="prefixClassname('forgot__field')"
        v-model.trim="state.password"
        name="password"
        placeholder="请设置新登录密码"
        :border="false"
        :type="state.passwordHidden ? 'password' : 'text'"
        :right-icon="state.passwordHidden ? 'closed-eye' : 'eye-o'"
        @click-right-icon="state.passwordHidden = !state.passwordHidden"
      />
      <van-field
        :class="prefixClassname('forgot__field')"
        v-model.trim="state.passwordAgain"
        name="passwordAgain"
        placeholder="请再次确认新登录密码"
        :border="false"
        :type="state.passwordAgainHidden ? 'password' : 'text'"
        :right-icon="state.passwordAgainHidden ? 'closed-eye' : 'eye-o'"
        @click-right-icon="state.passwordAgainHidden = !state.passwordAgainHidden"
      />
      <van-field
        :class="prefixClassname('forgot__field')"
        v-model.trim="state.code"
        name="code"
        placeholder="请输入验证码"
        :border="false"
      >
        <template #button>
          <captcha-link
            :sent="captchaSent"
            :time="60 * 1000"
            @send="sendCaptcha"
            @finish="captchaSent = false"
          >
            获得验证码
          </captcha-link>
        </template>
      </van-field>
      <div :class="prefixClassname('forgot__submit')">
        <van-button
          round
          type="primary"
          :loading="loading"
          :disabled="submitDisabled"
          @click="onSubmit"
        >
          完成
        </van-button>
      </div>
    </Form>
  </div>
</template>

<script>
import { computed, defineComponent, reactive, ref } from "vue"
import { Button, Field, Form, Toast } from "vant"
import Link from "@/components/Link"
import CaptchaLink from "../../components/CaptchaLink"
import { useRouter } from "vue-router"
import _ from "lodash"
import UserService from "../../services/user"
import TokenContext from "../../services/token"

const passwordReg = /^[0-9A-Za-z]{6,}$/

export default defineComponent({
  name: "PasswordReset",
  components: {
    CaptchaLink,
    [Link.name]: Link,
    [CaptchaLink.name]: CaptchaLink,
    [Button.name]: Button,
    [Form.name]: Form,
    [Field.name]: Field,
  },
  setup() {
    const { back } = useRouter()
    const loading = ref(false)
    const captchaSent = ref(false)

    const state = reactive({
      password: "",
      passwordAgain: "",
      code: "",
      passwordHidden: true,
      passwordAgainHidden: true,
    })

    const submitDisabled = computed(
      () => _.isEmpty(state.password) || _.isEmpty(state.passwordAgain) || _.isEmpty(state.code),
    )

    function onSubmit() {
      if (!passwordReg.test(state.password)) {
        Toast("密码必须由8~16位数字、字母任意组合")
        return
      }
      if (state.password !== state.passwordAgain) {
        Toast("两次登录密码不一致")
        return
      }
      const { password, passwordAgain, code } = state
      loading.value = true
      UserService.findUser({
        mobile: TokenContext.getToken().mobile,
        password,
        passwordAgain,
        code,
      })
        .then(({ status, message }) => {
          loading.value = false
          if (status === 200) {
            back()
          }
          Toast(message)
        })
        .finally(() => {
          loading.value = false
        })
    }

    function sendCaptcha() {
      const loadingToast = Toast({
        type: "loading",
        message: "发送中...",
        forbidClick: true,
        loadingType: "spinner",
      })
      UserService.sendCaptcha({ mobile: TokenContext.getToken().mobile, type: "find" })
        .then(({ status, message }) => {
          if (status === 200) {
            captchaSent.value = true
          }
          Toast(message)
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    return {
      loading,
      sendCaptcha,
      captchaSent,
      state,
      submitDisabled,
      onSubmit,
    }
  },
})
</script>

