<template>
  <div :class="prefixClassname('register')">
    <div :class="prefixClassname('register__header')">
      <div :class="prefixClassname('register__title')">注册</div>
      <div :class="prefixClassname('register__tips')">
        <van-icon name="info-o" />
        <span>国家和地区注册后不能更改</span>
      </div>
    </div>
    <van-form :class="prefixClassname('register__form')">
      <van-field
        :class="prefixClassname('register__field')"
        v-model.trim="mobile"
        name="mobile"
        type="tel"
        placeholder="请输入手机号"
        :border="false"
      >
        <template #label>
          <span :class="prefixClassname('register__field__van-label')">
            +86 <van-icon name="arrow-down" />
          </span>
        </template>
      </van-field>
      <div :class="prefixClassname('register__next')">
        <van-button round type="primary" :loading="loading" @click="onNext">下一步</van-button>
      </div>
    </van-form>
    <div :class="prefixClassname('register__footer')">
      继续注册即代表同意
      <Link to="/agreements/service">《创盈超算用户协议》</Link>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from "vue"
import { useRouter } from "vue-router"
import { prefixClassname } from "../../styles/prefix"
import Link from "../../components/Link"
import { Button, Field, Form, Icon, Toast } from "vant"
import CaptchaLink from "../../components/CaptchaLink"
import UserService from "../../services/user"

export default defineComponent({
  name: "UserRegisterStep1",
  components: {
    [Link.name]: Link,
    [CaptchaLink.name]: CaptchaLink,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [Form.name]: Form,
    [Field.name]: Field,
    [Field.name]: Field,
  },
  props: {
    inviteCode: String,
  },
  setup(props) {
    const loading = ref(false)
    const { push } = useRouter()
    const mobile = ref("")

    function onNext() {
      if (!/^1[3456789]\d{9}$/.test(mobile.value)) {
        Toast("请输入正确的手机号")
        return
      }

      loading.value = true
      UserService.checkMobile(mobile.value).then((body) => {
        loading.value = false
        if (body.status === 200) {
          Toast(body.message)
        } else {
          push({
            path: "/register",
            query: {
              steps: 2,
              mobile: mobile.value,
              inviteCode: props.inviteCode,
            },
          })
        }
      })
    }

    return {
      prefixClassname,
      loading,
      mobile,
      onNext,
    }
  },
})
</script>
