<template>
  <div :class="prefixClassname('register')">
    <div :class="prefixClassname('register__header')">
      <div :class="prefixClassname('register__title')">设置密码</div>
      <div :class="prefixClassname('register__tips')">
        <van-icon name="info-o" />
        <span>密码由8~16位数字、字母任意组合</span>
      </div>
    </div>
    <van-form
      :class="[prefixClassname('register__form'), prefixClassname('register__form--submit')]"
    >
      <div :class="prefixClassname('register__field-label')">1.设置登录密码</div>
      <van-field
        :class="prefixClassname('register__field')"
        v-model.trim="state.password"
        name="password"
        placeholder="请设置登录密码"
        :border="false"
        :type="state.passwordHidden ? 'password' : 'text'"
        :right-icon="state.passwordHidden ? 'closed-eye' : 'eye-o'"
        @click-right-icon="state.passwordHidden = !state.passwordHidden"
      />
      <div :class="prefixClassname('register__field-label')">2.确认登录密码</div>
      <van-field
        :class="prefixClassname('register__field')"
        v-model.trim="state.passwordAgain"
        name="passwordAgain"
        placeholder="请再次确认登录密码"
        :border="false"
        :type="state.passwordAgainHidden ? 'password' : 'text'"
        :right-icon="state.passwordAgainHidden ? 'closed-eye' : 'eye-o'"
        @click-right-icon="state.passwordAgainHidden = !state.passwordAgainHidden"
      />
      <div :class="prefixClassname('register__field-label')">3.设置交易密码</div>
      <van-field
        :class="prefixClassname('register__field')"
        v-model.trim="state.paypassword"
        name="paypassword"
        placeholder="请设置交易密码"
        :border="false"
        :type="state.paypasswordHidden ? 'password' : 'text'"
        :right-icon="state.paypasswordHidden ? 'closed-eye' : 'eye-o'"
        @click-right-icon="state.paypasswordHidden = !state.paypasswordHidden"
      />
      <div :class="prefixClassname('register__field-label')">4.确认交易密码</div>
      <van-field
        :class="prefixClassname('register__field')"
        v-model.trim="state.paypasswordAgain"
        name="passwordAgain"
        placeholder="请再次确认交易密码"
        :border="false"
        :type="state.paypasswordAgainHidden ? 'password' : 'text'"
        :right-icon="state.paypasswordAgainHidden ? 'closed-eye' : 'eye-o'"
        @click-right-icon="state.paypasswordAgainHidden = !state.paypasswordAgainHidden"
      />
      <div :class="prefixClassname('register__field-label')">5.短信验证码</div>
      <van-field
        :class="prefixClassname('register__field')"
        v-model.trim="state.code"
        name="code"
        :border="false"
        placeholder="请输入验证码"
      >
        <template #button>
          <captcha-link
            :sent="captchaSent"
            :time="60 * 1000"
            @send="sendCaptcha"
            @finish="captchaSent = false"
          >
            获得验证码
          </captcha-link>
        </template>
      </van-field>
      <div :class="prefixClassname('register__field-label')">邀请码[必填]</div>
      <van-field
        :class="[prefixClassname('register__field'), 'register__field__invite-code']"
        v-model.trim="state.inviteCode"
        name="inviteCode"
        placeholder="请输入邀请码"
        :border="false"
        :readonly="inviteCodeReadonly"
      />
      <div :class="prefixClassname('register__submit')">
        <van-button
          round
          type="primary"
          :loading="loading"
          :disabled="submitDisabled"
          @click="onSubmit"
        >
          完成
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, reactive, ref, watchEffect } from "vue"
import Link from "@/components/Link.vue"
import { Button, Field, Form, Icon, Toast } from "vant"
import { prefixClassname } from "@/styles/prefix"
import CaptchaLink from "@/components/CaptchaLink.vue"
import _ from "lodash"
import UserService from "@/services/user"
import { useRouter } from "vue-router"

const passwordReg = /^[0-9A-Za-z]{6,}$/

export default defineComponent({
  name: "UserRegisterStep2",
  components: {
    CaptchaLink,
    [Link.name]: Link,
    [CaptchaLink.name]: CaptchaLink,
    [Button.name]: Button,
    [Icon.name]: Icon,
    [Form.name]: Form,
    [Field.name]: Field,
    [Field.name]: Field,
  },
  props: {
    mobile: String,
    inviteCode: String,
  },
  setup(props) {
    const { push } = useRouter()
    const loading = ref(false)
    const captchaSent = ref(false)
    const inviteCodeReadonly = ref(false)

    const state = reactive({
      password: "",
      passwordAgain: "",
      paypassword: "",
      paypasswordAgain: "",
      code: "",
      inviteCode: "",
      passwordHidden: true,
      passwordAgainHidden: true,
      paypasswordHidden: true,
      paypasswordAgainHidden: true,
    })

    watchEffect(() => {
      if (props.inviteCode) {
        state.inviteCode = props.inviteCode
        inviteCodeReadonly.value = true
      }
    })

    function onSubmit() {
      if (_.isEmpty(state.inviteCode)) {
        Toast("请输入邀请码")
        return
      }
      if (!passwordReg.test(state.password)) {
        Toast("密码必须由8~16位数字、字母任意组合")
        return
      }
      if (!passwordReg.test(state.paypassword)) {
        Toast("密码必须由8~16位数字、字母任意组合")
        return
      }
      if (state.password !== state.passwordAgain) {
        Toast("两次登录密码不一致")
        return
      }
      if (state.paypassword !== state.paypasswordAgain) {
        Toast("两次交易密码不一致")
        return
      }
      const { password, passwordAgain, paypassword, paypasswordAgain, code, inviteCode } = state
      loading.value = true
      UserService.registerUser({
        mobile: props.mobile,
        password,
        passwordAgain,
        paypassword,
        paypasswordAgain,
        code,
        inviteCode,
      }).then(({ status, message }) => {
        loading.value = false
        if (status === 200) {
          push("/login")
        }
        Toast(message)
      })
    }

    const submitDisabled = computed(
      () =>
        _.isEmpty(state.password) ||
        _.isEmpty(state.passwordAgain) ||
        _.isEmpty(state.paypassword) ||
        _.isEmpty(state.paypasswordAgain) ||
        _.isEmpty(state.code) ||
        _.isEmpty(state.inviteCode),
    )

    function sendCaptcha() {
      const loadingToast = Toast({
        type: "loading",
        message: "发送中...",
        forbidClick: true,
        loadingType: "spinner",
      })
      UserService.sendCaptcha({ mobile: props.mobile, type: "register" }).then(
        ({ status, message }) => {
          loadingToast.clear()
          if (status === 200) {
            captchaSent.value = true
          }
          Toast(message)
        },
      )
    }

    return {
      prefixClassname,
      state,
      loading,
      captchaSent,
      inviteCodeReadonly,
      sendCaptcha,
      submitDisabled,
      onSubmit,
    }
  },
})
</script>
<style lang="scss">
.register__field__invite-code {
  background: #e6f7ff !important;

  .van-field__control {
    text-align: center;
  }
}
</style>
